export default [{"image_name":"A Thousand Rasps.jpg","display_name":"A Thousand Rasps","website_link":"http://athousandrasps.com","tags":["Bags / Purses / Pouches","Bracelets / Cuffs / Wristbands","Masks / Costuming","Clothing / Hats / Shirts / Pants / Leggings","Mugs / Cups / Ceramics / Coasters","Artwork (Originals / Prints / Commissions)"],"short_description":"A Thousand Rasps provides tooled leather goods for Renaissance faires, costumes, and everyday wear. Everything is crafted by hand from veg-tan leather, from carving and stamping to staining and assembly. I primarily focus on accessories like belt pouches, bracelets, and armor.","":""},{"image_name":"AbandonAmbition_FormerlyZelaphas - Dana Larson - AC2021_VRDR.jpg","display_name":"Abandon Ambition (formerly Zelphas)","website_link":"https://abandonambition.etsy.com","tags":["Artwork (Originals / Prints / Commissions)","Keychains / Charms / Luggage Tags","Stickers / Clings / Magnets"],"short_description":"Abandon Ambition focuses on animals and mythical beasts, with an emphasis on their teeth and more sinister features. ","":""},{"image_name":"Aisu Art (Rebecca Rodriguez) - aisuart-booth.jpg","display_name":"Aisu Art","website_link":"https://www.aisuart.com/","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Stickers / Clings / Magnets","Keychains / Charms / Luggage Tags","Cloth Face Masks","Lanyards","Bracelets / Cuffs / Wristbands"],"short_description":"Rebecca Kate Rodriguez is an artist, toy collector, and theme park enthusiast located in Orlando, FL. She's been drawing since she could hold a pencil and she uses her fascination with animals and internal anatomy to express herself and explore her (a)sexuality. Under username Aisu (EYE-soo), she's gained a following for her art, writing, and cosplay. Now offering commissions, she graduated 2016 with a degree in computer animation, and is now self-employed, selling her art online and at conventions under the business name Aisu Art.","":""},{"image_name":"Animal Skull Masks - Eric Strother.jpg","display_name":"Animal Skull Masks by Eric Strother","website_link":"http://animalskullmasks.com","tags":["Masks / Costuming","Artwork (Originals / Prints / Commissions)","Sculpture / Figurines"],"short_description":"Resin and 3D-Printed Masks - Each resin mask starts as an idealized sculpture, and is molded to create light polyurethane casting. 3D-printed masks are digitally sculpted by hand in VR using Oculus touch controllers, then printed using PLA filament and coated with a durable epoxy. Acrylic and oil paints are used to paint each mask in beautiful detail. The adjustable D-ring fasteners of the cotton or elastic straps makes for a perfect fit.","":""},{"image_name":"Apricity Hats - Amy Allred - booth_AC VRDR.jpg","display_name":"Apricity Hats & Cosplay Wear","website_link":"https://www.etsy.com/shop/apricityhats","tags":["Clothing / Hats / Shirts / Pants / Leggings","Pillows / Pillowcases","Plush Animals / Toys","Bandanas","Necklaces / Chokers / Collars","Bags / Purses / Pouches"],"short_description":"Here in Apricity Hats and Cosplay Wear I sell handmade fleece hats with 3D ears. I get my inspiration through real animals, film and comic characters, as well as the personal characters owned and loved by individuals of the anthropomorphic and anime fandoms. I am open to custom orders.","":""},{"image_name":"ARGIBI Creative Studio - Nathan Adams - VRC-02-2048x2048.jpg","display_name":"AR.GI.BI. Creative Studio","website_link":"https://www.argibi.art/","tags":["Artwork (Originals / Prints / Commissions)","Mousepads","Pillows / Pillowcases","Stickers / Clings / Magnets","Buttons / Pins","Bags / Purses / Pouches","Clothing / Hats / Shirts / Pants / Leggings","Mugs / Cups / Ceramics / Coasters","Lanyards","Bracelets / Cuffs / Wristbands","Bandanas","Cloth Face Masks"],"short_description":"BLUEHALO, the main artist behind ArGiBi Arts, is an illustrator, designer and classically trained animator currently working as an independent and freelance artist. They feel most at home creating works featuring vivid colors and incorporating vintage aesthetics with cute animal characters, making for the perfect theme as they explore who they are and where their artistic journey is taking them.","":""},{"image_name":"The Art of Nicole - Nicole (Nicnak) - booth.jpg","display_name":"The Art Of Nicole","website_link":"https://www.theartofnicole.com/","tags":["Keychains / Charms / Luggage Tags","Artwork (Originals / Prints / Commissions)","Mugs / Cups / Ceramics / Coasters","Cloth Face Masks","Stickers / Clings / Magnets","Buttons / Pins","Bracelets / Cuffs / Wristbands"],"short_description":"Nicole, more well known to others as ‘Niki’ or ‘Nicnak, is a 26 year old illustrator and designer, specializing in character and prop design. She dedicates herself to commissioned and personal projects, sharing her work on social media sites, and creating one of a kind merchandise for sale online and at conventions she attends.","":""},{"image_name":"Bill Holbrook - Teri Holbrook - Holbrook.jpg","display_name":"Bill Holbrook","website_link":"https://www.kevinandkell.com/","tags":["Comics / Graphic Novels"],"short_description":"Bill Holbrook created \"Kevin and Kell\" back in 1995 because he wanted to contribute something original and fun for the online community to read: \"Years ago, Kevin Kindle the rabbit and Kell Dewclaw the wolf met in an online chat room. After falling head over heels for each other, they decided to meet in person. It wasn't until then that they realized they were from separate ends of the food chain.\"","":""},{"image_name":"Blue Canary Gallery - blue_canary_gallery-booth.jpg","display_name":"Blue Canary Gallery","website_link":"https://www.etsy.com/shop/bluecanarygallery","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Keychains / Charms / Luggage Tags","Bandanas","Lanyards","Pillows / Pillowcases","Earrings / Body Jewelry","Stickers / Clings / Magnets","Fursuits / Fursuit parts / Fursuit Accessories"],"short_description":"Blue Canary Gallery creates collectible fursuit snap-and-go bandanas, illustrations, enamel pins, key chains, lanyards, and rings, vinyl stickers, and wearable art designs.","":""},{"image_name":"Bubbly Sheep Creations - Savanna Collector - Booth.jpg","display_name":"Bubbly Sheep Creations","website_link":"https://www.etsy.com/shop/BubblySheepCreations","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Stickers / Clings / Magnets","Keychains / Charms / Luggage Tags","Earrings / Body Jewelry","Lanyards"],"short_description":"Savanna Collector age 28 located in sunny Florida. Graduated from Ringling college of Art and Design in 2014 with a BFA in Illustration. Currently a freelance artist, selling online and at a wide selection of conventions across the states.","":""},{"image_name":"Camp Mustelid.jpg","display_name":"Camp Mustelid","website_link":"https://nepeteaa.bigcartel.com","tags":["Artwork (Originals / Prints / Commissions)","Clothing / Hats / Shirts / Pants / Leggings","Patches","Stickers / Clings / Magnets","Buttons / Pins"],"short_description":"This is an extension of our project, Camp Mustelid! (https://www.campmustelid.com/) Camp Mustelid is run by two LGBT artists from the woods now residing in Cincinnati Ohio! All the proceeds from our shop help us to continue making fun nature themed products and afford us more time to come up with fun activities for CM! A lot of our items and bundles come with Camp Mustelid badges! We send an info card with each order, but if you'd like to read more about the badges and their significance, see https://www.campmustelid.com/badges","":""},{"image_name":"Carpetshark Studio - Ryan Hudson - VRDR.jpg","display_name":"Carpetshark Studio","website_link":"https://linktr.ee/dook","tags":["Artwork (Originals / Prints / Commissions)"],"short_description":"I'm Dook and have been an avid geek of various sorts in many facets. I create art that is mostly SFW, but also occasionally NSFW. I have kept a promise to myself since I started posting art online that I would always post my personal projects and un-commissioned art for everybody to see for free. I've never wanted to put any of my content behind a pay wall & lock out folks from seeing my work.","":""},{"image_name":"Cashmere Sky - Jared Hudson - booth.jpg","display_name":"Cashmere Sky","website_link":"https://cashmeresky.com/","tags":["Comics / Graphic Novels"],"short_description":"Cashmere Sky is a fantasy adventure graphic novel attempting to recreate the magic of what cartoons and animation did for us growing up. It’s described as a retro-future world with “solarpunk” tones bringing together an exciting brew of airships, electricity, and an alien race of cat-like people. The project is heavily inspired by TV and feature film animation pulling from many twentieth century trends.","":""},{"image_name":"CCS Mascots Craig Ames Booth Layout.jpg","display_name":"CCS Mascots","website_link":"https://ccsmascots.com/","tags":["Fursuits / Fursuit parts / Fursuit Accessories"],"short_description":"Located in Central Maine, we make custom costumes, costume parts, and accessories. We have been catering to fans of the anthropomorphic and mascots alike since 2014!","":""},{"image_name":"Chakat Windshear's Wares - Keith Biscanti - VR den booth.jpg","display_name":"Chakat Windshear's Wares","website_link":"https://www.windshearswares.com/xcart/","tags":["Artwork (Originals / Prints / Commissions)","Books / Magazines","Comics / Graphic Novels","Pillows / Pillowcases","Plush Animals / Toys"],"short_description":"We have the largest selection of furry Japanese doujinshi with over 1000 titles and a large selection of Anthropomorphic items for sale.","":""},{"image_name":"Coey and Shy - Coey Kuhn [] - COEYandSHY_CoeyKuhn_booth.jpg","display_name":"Coey & Shy","website_link":"https://www.etsy.com/shop/CoeyAndShy","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Stickers / Clings / Magnets","Keychains / Charms / Luggage Tags","Patches","Lanyards","Books / Magazines","Clothing / Hats / Shirts / Pants / Leggings","Cloth Face Masks","Bags / Purses / Pouches","Bandanas","Mousepads","Mugs / Cups / Ceramics / Coasters","Pillows / Pillowcases","Bracelets / Cuffs / Wristbands"],"short_description":"Shy Custis is an illustrator based in Columbus, Ohio. She works primarily in pencil, with digital finishing in Photoshop. Ohio based artist Coey Kuhn - 10.08.87. MALE. - COEYKUHN @ TWITTER - Commissions: closed at the moment. Makes too many DnD OCs he'll never use.","":""},{"image_name":"Cooner Arts - John Nunnemacher - irtualbooth.jpg","display_name":"Cooner Arts!","website_link":"http://cooner.johntoons.com","tags":["Artwork (Originals / Prints / Commissions)","Comics / Graphic Novels","Clothing / Hats / Shirts / Pants / Leggings","Mugs / Cups / Ceramics / Coasters","Bags / Purses / Pouches","Stickers / Clings / Magnets"],"short_description":"Cooner was born to a family of raccoons in the Lehigh Valley, just north of the Philadelphia area of eastern Pennsylvania. Growing up in rural suburbia, he entertained a number of interests through childhood, including science, computers, cooking, and architecture. Ultimately, though, he would be destined to draw cartoons and funny animals.","":""},{"image_name":"Corgilegs (Gel) - corgilegs_booth.jpg","display_name":"Corgilegs","website_link":"http://corgilegs.pizza","tags":["Artwork (Originals / Prints / Commissions)","Fursuits / Fursuit parts / Fursuit Accessories","Stickers / Clings / Magnets","Patches"],"short_description":"I’m Gel, also known as Corgilegs! I’m an artist living in the Pacific Northwest, creating costumes, illustrations, and a variety of other crafts! I have been creating custom, one of a kind mascot costumes since 2006! My specialty is cats, but I can create anything you can think of!","":""},{"image_name":"CosmellCosplay - Samantha Lineberry - booth-NEW.jpg","display_name":"CosmellCosplay","website_link":"https://www.cosmellcosplay.com","tags":["Fursuits / Fursuit parts / Fursuit Accessories","Artwork (Originals / Prints / Commissions)","Lanyards","Keychains / Charms / Luggage Tags","Stickers / Clings / Magnets","Buttons / Pins","Clothing / Hats / Shirts / Pants / Leggings"],"short_description":"Hello! My name is Samantha, I’m 20 and Pansexual, and I am the artist behind CosmellCosplay! I make fursuits, cosplay, digital and traditional art, merch, fursuit parts, and more!","":""},{"image_name":"Curlworks - curlworks-booth.jpg","display_name":"Curlworks","website_link":"http://curlworks.net","tags":["Pajamas / Kigurumis","Plush Animals / Toys","Clothing / Hats / Shirts / Pants / Leggings","Stickers / Clings / Magnets","Buttons / Pins","Fursuits / Fursuit parts / Fursuit Accessories"],"short_description":"Hey there! We’re Curlworks– a Twin Cities based indie artist collab that specializes in handmade custom kigurumis, plush accessories and props, and ethically sourced and hand-crafted funky merch. Founded in 2015 by two artists, we're very excited to craft some good things just for you. Please have a browse and reach out if you wanna!","":"Dealer requests that we use the Anthrocon booth shape, not the BLFC booth shape."},{"image_name":"DaSueDragon Designs - Sue Maple -BANNER.jpg","display_name":"DaSueDragon Designs","website_link":"https://www.etsy.com/shop/DaSueDragon","tags":["Bracelets / Cuffs / Wristbands","Masks / Costuming","Bags / Purses / Pouches","Keychains / Charms / Luggage Tags"],"short_description":"Thanks for choosing DaSueDragon Designs!! I'm here to make safe, fun, and creative works of art for all to enjoy. Mainly, my leather and prop work is said to be considered child-safe and is quite durable. Meant to be beaten and worn, my props are made to be accepted into ANY convention and to be used for fun and play. Not so much a \"wall-hanger\" as it's something to pick up and wack your friend with. :)","":""},{"image_name":"David Silver - Dealer's den template.jpg","display_name":"David Silver","website_link":"http://books.ponyfinder.net/","tags":["Games (Board / Video / Tabletop)"],"short_description":"Silver Games LLC, creators of Ponyfinder and Tails of the City, letting you wag a tail in Dungeons and Dragons and Pathfinder as you always wanted to.","":""},{"image_name":"DiZZiNESS - DiZZiNESS-update.jpg","display_name":"DiZZiNESS","website_link":"https://www.furaffinity.net/gallery/dizziness/","tags":["Artwork (Originals / Prints / Commissions)","Plush Animals / Toys"],"short_description":"Myth, magic, mystery! Artist-made traditional media & fiber arts: adoptables, artdolls, enamel/acrylic pins, charms, stickers, commissions and more... Avian Friendly & Hoofer Approved. https://twitter.com/thedizziness","":""},{"image_name":"Dojerodesigns-booth.png - BB111EB0-107E-4102-A6D8-E0027FAF4134.jpg","display_name":"DojeroDesigns","website_link":"https://dojerodesigns.com/","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Cloth Face Masks","Pillows / Pillowcases","Keychains / Charms / Luggage Tags","Mousepads","Stickers / Clings / Magnets"],"short_description":"Hey Ya'll, I'm Dojero! I enjoy cottage core, cryptids, anime and napping. I found my love of creating character illustrations in 2015 and have been doing them since! Merchendise designing is a new adventure as of 2019, and I can't get enough of it.","":""},{"image_name":"Dowidat Ceramics - Claypaws - Holly Dowidat - Booth.jpg","display_name":"Dowidat Ceramics","website_link":"http://www.dowidatceramics.com","tags":["Mugs / Cups / Ceramics / Coasters","Sculpture / Figurines"],"short_description":"Here at Dowidat Ceramics we strive for creating the best, unique, functional pottery. All glazes are made on site, the pottery thrown by my hands, each detail is done with all the carefulness creating pieces that are as unique as you.","":""},{"image_name":"Dragon's Lair, The - EDWARD TRACHTENBERG - DragonsLair.jpg","display_name":"The Dragon's Lair","website_link":"http://www.plushiezoo.biz","tags":["Plush Animals / Toys","Sculpture / Figurines","Clothing / Hats / Shirts / Pants / Leggings"],"short_description":"Our goal is to bring to you the very finest merchandise that focuses on the interests of the Science Fiction, Fantasy, Anime and Anthropomorphic communities. We Take Fantasy Very Seriously.","":""},{"image_name":"Drak Arts - Adrian Amarteifio - AC-Booth.jpg","display_name":"Drak-Arts","website_link":"http://www.drak-arts.com","tags":["Artwork (Originals / Prints / Commissions)","Books / Magazines","Comics / Graphic Novels"],"short_description":"I'm an African American artist from Idaho. I've been drawing since as long as I can remember, starting out trying to study anatomy books before expanding into an interest in various forms of animation and gaming. This developed into a specialty in character-focused design and illustration, while maintaining a grounded understanding of anatomy and semi-realism. I enjoy bouts of world-building and development of my own personally created characters.","":""},{"image_name":"Dream Vision Creations - Angela Sorrentino - booth2.jpg","display_name":"Dreamvision Creations","website_link":"http://dreamvisioncreations.com","tags":["Fursuits / Fursuit parts / Fursuit Accessories","Buttons / Pins"],"short_description":"Manufacturers of parts and pieces for do-it-yourself costume builders.","":""},{"image_name":"Fautbarb - Nicole Hayes - booth.jpg","display_name":"fautbarb","website_link":"https://www.etsy.com/shop/fautbarb","tags":["Artwork (Originals / Prints / Commissions)","Stickers / Clings / Magnets","Clothing / Hats / Shirts / Pants / Leggings","Mugs / Cups / Ceramics / Coasters","Bags / Purses / Pouches","Patches","Buttons / Pins","Keychains / Charms / Luggage Tags","Cloth Face Masks"],"short_description":"fautbarb started as a passion project for drawing terribly. It grew into a lovely business selling at conventions.","":""},{"image_name":"Floral Frolic - floralfrolic_vrbooth.jpg","display_name":"Floral Frolic","website_link":"https://floralfrolic.com/","tags":["Plush Animals / Toys","Artwork (Originals / Prints / Commissions)","Buttons / Pins","Books / Magazines","Bags / Purses / Pouches","Lanyards","Stickers / Clings / Magnets","Clothing / Hats / Shirts / Pants / Leggings","Keychains / Charms / Luggage Tags"],"short_description":"This shop is run by two artists, Amanda Coronado and Cari Corene! We are two artists who have been creating and running a business together and independently for over 7 years!  We started our business just selling art at anime cons, and now we have a full fledged online shop! Cari Corene is a watercolor artist hailing from Alaska and Washington State. She enjoys taking inspiration from the outdoors and nature. She likes to draw fat animals and Sailor Moon art. Amanda Coronado is an artist formerly from Texas. She enjoys photography and playing with toys. She like to draw fat animals and Fate art.","":""},{"image_name":"Furry Dakimakura - anthrocon-vrdr.jpg","display_name":"Furry Dakimakura","website_link":"https://www.furrydakimakura.com/","tags":["Pillows / Pillowcases","Mousepads","Cloth Face Masks","Artwork (Originals / Prints / Commissions)"],"short_description":"Furry Dakimakura: Your #1 Source for Furry Dakis! Please respect our need to stay within the law.","":""},{"image_name":"Furry Mystery Box - Alecia Weber - booth.jpg","display_name":"Furry Mystery Box","website_link":"http://www.furrymysterybox.com","tags":["Artwork (Originals / Prints / Commissions)","Books / Magazines","Clothing / Hats / Shirts / Pants / Leggings","Comics / Graphic Novels","Plush Animals / Toys","Sculpture / Figurines","Cloth Face Masks","Mugs / Cups / Ceramics / Coasters","Buttons / Pins","Stickers / Clings / Magnets","Lanyards","Bags / Purses / Pouches","Bandanas","Keychains / Charms / Luggage Tags"],"short_description":"We are a Cincinnati based company with years of experience in the Furry fandom, creating furry art, comics, games, toys and so much more! Our multiple business partnerships have enabled us to provide quality merchandise and print services to many different retailers and trade shows. We aim to provide the fandom with the highest quality furry merchandise with each box we release! These boxes are not simply reused, or wide scale available merchandise. Our team of artists and designers work with our business contacts to manufacture custom apparel, wearable items, pins, buttons, keychains, toys, plushes, food grade items and more.","":""},{"image_name":"Fursewna Studios - Anna Olson -Booth.jpg","display_name":"Fursewna Studios","website_link":"http://linktr.ee/Fursewnastudios","tags":["Artwork (Originals / Prints / Commissions)","Clothing / Hats / Shirts / Pants / Leggings","Buttons / Pins","Stickers / Clings / Magnets","Fursuits / Fursuit parts / Fursuit Accessories"],"short_description":"Fursewna Studios brings to life many different forms of creatures. We design creatures, we make creatures, we are creatures (maybe). We specialize in Fursuits, Onesie Pajamas, Hoodies, Hats, Plush works, Props, Reference Sheets, Art and so much more! If you're looking for a place to bring your animals to life and work professionally and on time till the job is done, look no further!","":""},{"image_name":"Fursona Pins - FursonaPins AC21 VRDR 72dpi.jpg","display_name":"Fursona Pins","website_link":"http://www.fursonapins.com/shop","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Stickers / Clings / Magnets","Cloth Face Masks","Lanyards","Plush Animals / Toys","Bracelets / Cuffs / Wristbands","Keychains / Charms / Luggage Tags","Bags / Purses / Pouches","Clothing / Hats / Shirts / Pants / Leggings"],"short_description":"Fursona Pins are a collection of hard enamel pins based off animal characters, as commissioned by their owners. When slots open, you can become part of the Fursona Pins family by commissioning your character as a Pin. Because these are a collection, your order is entirely handled by us - artwork production by our collection artist Kiwiggle (Kiwibon.co.uk), manufacturing, and shipping directly to you. It's all inclusive! Fursona Pins also features a product called Pin of the Month - which allows you to subscribe to our production business and receive brand new enamel pins every month, directly to your home. It's a great way to get your Fursona Pins collection started.","":""},{"image_name":"Fursuit-Glasses-SamuelRocazella - FursuitGlasses-booth.jpg","display_name":"FursuitGlasses","website_link":"https://www.fursuitglasses.com/","tags":["Fursuits / Fursuit parts / Fursuit Accessories"],"short_description":"If your fursona wears glasses, your fursuit should have the opportunity to as well. We’ve set out to create that option for people. While there are options available, most are via kids toys. Why not have proper glasses tailored to fursuit heads?","":""},{"image_name":"Fursuit Supplies - Ellen Baker booth.jpg","display_name":"Fursuit Supplies Dot Com","website_link":"https://fursuitsupplies.com/","tags":["Pajamas / Kigurumis","Fursuits / Fursuit parts / Fursuit Accessories","Cloth Face Masks"],"short_description":"We are a one-stop-shop for fursuit supplies such as faux fur, balaclavas, and more! ","":""},{"image_name":"Goal Publications - Sean Gerace - booth.jpg","display_name":"Goal Publications","website_link":"https://www.goalpublications.com/","tags":["Artwork (Originals / Prints / Commissions)","Books / Magazines","Music","Clothing / Hats / Shirts / Pants / Leggings","Bags / Purses / Pouches","Stickers / Clings / Magnets"],"short_description":"Goal Publication is a queer-owned fiction book publisher from Norwich, Connecticut, and is Eastern USA's only furry book publisher. Goal Publications was started in 2015 and has since pushed to be a market that wholeheartedly supports authors from all over the queer spectrum, and is pushing hard to find and publish talented BIPOC authors, as well as other marginalized authors.","":""},{"image_name":"GryphonGlassware.png - Amy Bell.jpg","display_name":"Gryphon Glassware","website_link":"http://gryphonglassware.etsy.com","tags":["Sculpture / Figurines","Mugs / Cups / Ceramics / Coasters","Buttons / Pins"],"short_description":"Need some drinkware with your favorite character or animal species on it? Or maybe something more custom? We have that! Be it Pint glasses, Shot Glasses, Water Bottles, or flask we have something for everyone.","":""},{"image_name":"Heliopause Productions - Grace Ogawa - AC21Boothv2.jpg","display_name":"Heliopause Productions","website_link":"https://heliopauseweb.square.site","tags":["Artwork (Originals / Prints / Commissions)","Books / Magazines","Stickers / Clings / Magnets","Keychains / Charms / Luggage Tags"],"short_description":"Heliopause Productions is the one-woman art and writing show run by Goldeen Ogawa. Here you can order signed books, prints, and other special items. From far flung science fiction to dark fantasy and everything in between, our shop is packed with thrilling adventures and transportive artwork.","":""},{"image_name":"hibbary - Hillary Luetkemeyer - virtualtable.jpg","display_name":"hibbary","website_link":"https://www.furaffinity.net/user/hibbary/","tags":["Artwork (Originals / Prints / Commissions)"],"short_description":"I've been drawing for years, but have only just started doing it professionally. My studio background is actually in metalsmithing, but I love drawing and illustration. You can get most of my work in the form of prints. A 8 1/2 by 11 print costs $10 plus shipping. Note me if you are interested!","":""},{"image_name":"Hibiscus Stitch - HIBISCUSSTITCH-BOOTH.jpg","display_name":"Hibiscus Stitch","website_link":"https://www.hibiscusstitch.com/","tags":["Plush Animals / Toys","Stickers / Clings / Magnets","Keychains / Charms / Luggage Tags"],"short_description":"Artist Tuesday Pope is a business savvy woman with a flair for creativity. While her love for business started from birth (she would often be found pretending to own a store on the playground with perhaps far too much accuracy for one so young) as did her love for video games, her plush toy sewing began around the age of 15. Michael Sullivan is many things, loving fiance, amazing artist, and destroyer of dreams at any game he touches. He's the behind the scenes guy. Most of the buttons are created or arranged by him, most con table set ups he has a hand in ziptie-ing and constructing, and he has a knack for product ideas.","":""},{"image_name":"Hopeful Monster Studios - Dani Walker -booth.jpg","display_name":"Hopeful Monster Studios","website_link":"http://www.hopefulmonsterstudios.com","tags":["Stickers / Clings / Magnets","Buttons / Pins","Sculpture / Figurines","Artwork (Originals / Prints / Commissions)","Plush Animals / Toys","Games (Board / Video / Tabletop)","Earrings / Body Jewelry"],"short_description":"D. Walker is the human shaped vessel of Hopeful Monster Studios. D. began making art professionally in 2010, then launched the Cruinndrac Farms Wyrm Game in 2012. She enjoys furthering her skills in sculpting & 2d art, as well as costume making, leatherworking, wood carving, & any other random art supplies she can get her hands on.","":""},{"image_name":"Houndsaint - Katherine Vasey - AC2021 VDealers Booth.jpg","display_name":"Houndsaint","website_link":"https://www.etsy.com/shop/HoundSaint","tags":["Artwork (Originals / Prints / Commissions)","Patches","Buttons / Pins","Lanyards","Bandanas","Cloth Face Masks","Keychains / Charms / Luggage Tags","Bracelets / Cuffs / Wristbands","Bags / Purses / Pouches"],"short_description":"I'm a formally educated artist & self-taught linocut printmaker based in Columbus, OH and my primary inspirations are 10th-15th century religious art and greyhounds. I've been doing relief printing for about 3 years.","":""},{"image_name":"inanimorphs-booth-test.jpg","display_name":"Inanimorphs Art and Furry Flags","website_link":"https://www.inanimorphs.com/","tags":["Comics / Graphic Novels","Games (Board / Video / Tabletop)","Clothing / Hats / Shirts / Pants / Leggings","Buttons / Pins","Cloth Face Masks","Mugs / Cups / Ceramics / Coasters","Books / Magazines","Artwork (Originals / Prints / Commissions)","Pillows / Pillowcases","Bags / Purses / Pouches","Stickers / Clings / Magnets"],"short_description":"Professional artist, published author, and historian known for original, freelance, and fan work. Long time veteran artist of the convention vending scene from comic conventions to furry cons. Specializes in enamel pin, coins, playing card, coat of arms, seals, and flag designs. Freelance work done for Arcadia Publishing, ACME Pins, Legend Toys, Disney, IDW, Top Shelf, 4DE, and various companies, schools, military units. Raised in a traditional Taoist shamanic family, the artist later studied genetic biology at Johns Hopkins, and now spends full time work decided to drawing and writing history books. ","":""},{"image_name":"Ishaway - Ishaway Friestad - AC-booth 2.jpg","display_name":"Ishaway","website_link":"http://www.ishaway.com","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Stickers / Clings / Magnets","Clothing / Hats / Shirts / Pants / Leggings"],"short_description":"I am an visual illustrator, working in a variety of medias, both traditional and digital. I have shown my work at fantasy conventions and a couple of art shows. More of my work can be found on my Tumblr, thedrawingunicorn.tumblr.com Or on my Weasyl gallery, www.weasyl.com/~ishaway I also maintain a RebBubble store front for the production of items currently beyond my ability to make and sell directly. www.redbubble.com/people/ishaway/shop","":""},{"image_name":"JenKiwi - jenkiwi-booth.jpg","display_name":"JenKiwi","website_link":"https://jenkiwi.square.site","tags":["Artwork (Originals / Prints / Commissions)","Sculpture / Figurines"],"short_description":"Small nocturnal bird. Artist, writer, programmer. Sculptor of tiny critters. Powered by coffee & Bueno bars. She/Her On FA & DA as JenKiwi On IG as jenkiwi_art","":""},{"image_name":"Kemono Cafe - ron rod - 2021-06-21 21.47.56 - booth.jpg","display_name":"Kemono Cafe","website_link":"https://kemono.cafe/","tags":["Comics / Graphic Novels","Sculpture / Figurines"],"short_description":"We are a webcomic host that tries to differentiate itself with its own content. The content is partially created by our members through donating characters to a fictional town called Fable. We are a webhost with a cast of characters we do things with, just like a webcomic, if that makes sense. Kemono Cafe is owned by SKAMP, LLC. We’re a group of comic fans that wanted to help our friends host their creative works while also doing something fun for ourselves.","":""},{"image_name":"Kevlar Artworks - Fluff Kevlar - booth.jpg","display_name":"Kevlar Artworks","website_link":"http://rishangalaxy.com","tags":["Artwork (Originals / Prints / Commissions)","Books / Magazines","Pillows / Pillowcases","Bracelets / Cuffs / Wristbands","Stickers / Clings / Magnets"],"short_description":"Artist, Gamer, Bluejay Gryphon. If you like my work, consider supporting me on http://Patreon.com/fluffkevlar","":""},{"image_name":"King Guro - KingGuro-Booth.jpg","display_name":"King Guro","website_link":"http://www.krisstarlein.com","tags":["Patches","Artwork (Originals / Prints / Commissions)","Buttons / Pins","Clothing / Hats / Shirts / Pants / Leggings","Keychains / Charms / Luggage Tags"],"short_description":"The work I do is unique from others stylistically. I enjoy working with others as a team, and I’m open to working a number design related jobs including product / merch design, album covers, posters, tattoos, comics, concepts, animations, editorials, teaching, and freelancing.","":""},{"image_name":"Lemonbrat - lemonbrat-booth.jpg","display_name":"Lemonbrat Studios","website_link":"https://lemonbrat.com/","tags":["Fursuits / Fursuit parts / Fursuit Accessories","Pajamas / Kigurumis","Clothing / Hats / Shirts / Pants / Leggings","Cloth Face Masks","Bags / Purses / Pouches","Buttons / Pins"],"short_description":"Lemonbrat is a collection of crafters, artists, and seamstresses who create fursuits, hoodies, kigurumi, and masks. Everything on our site is designed, and hand-made by our talented team, in Chicago studio. Because we hand-make everything as you order it we are able to offer a wide range of selections to accommodate custom fits, colors, and details! No mass-manufacturer can offer you that level of individual care! :3","":""},{"image_name":"Lemon-CremeStudios - Pudding Cup - _Booth.jpg","display_name":"Lemon-Crème Studios","website_link":"https://www.etsy.com/shop/LemonCremeStudios/","tags":["Pillows / Pillowcases","Mousepads","Sculpture / Figurines","Plush Animals / Toys","Keychains / Charms / Luggage Tags","Mugs / Cups / Ceramics / Coasters","Lanyards","Stickers / Clings / Magnets"],"short_description":"We are two artists based in Southern California with a passion for cute and furry. We create and sell artwork online as well as at conventions.","":""},{"image_name":"LunarAchilles - Nem Mueske - booth.jpg","display_name":"LunarAchilles","website_link":"http://linktr.ee/lunarachilles","tags":["Artwork (Originals / Prints / Commissions)","Keychains / Charms / Luggage Tags","Buttons / Pins","Stickers / Clings / Magnets","Lanyards"],"short_description":"Hi! I'm Nem and I'm an illustrator who loves anime and video games. Check out my shop and consider supporting my art!","":""},{"image_name":"Mango Island - Shannon Deeds - booth.jpg","display_name":"Mango Island","website_link":"https://www.etsy.com/shop/MangoIsland","tags":["Clothing / Hats / Shirts / Pants / Leggings","Buttons / Pins","Patches","Plush Animals / Toys"],"short_description":"I specialize in Embroidery work, patches, caps, you name it! I also have been known to make custom kigurumis, follow me on Twitter for more info! Thanks for stopping by!","":""},{"image_name":"Marci McAdam - MarciMcAdam-booth.jpg","display_name":"Marci McAdam","website_link":"http://www.theartofmarci.com/furries/","tags":["Artwork (Originals / Prints / Commissions)","Stickers / Clings / Magnets","Books / Magazines","Clothing / Hats / Shirts / Pants / Leggings","Mugs / Cups / Ceramics / Coasters"],"short_description":"Cute, colourful, and bursting with joy–if you’re looking for art featuring adorable animals, you’ve come to the right place. I love drawing furry characters of all ages, but I’m especially known for my “littles”.","":""},{"image_name":"Mary Mouse's Certified Tags - marymouse-booth.jpg","display_name":"Mary Mouse's Certified Tags","website_link":"https://marymouse.storenvy.com/","tags":["Artwork (Originals / Prints / Commissions)","Stickers / Clings / Magnets","Buttons / Pins"],"short_description":"Mary Mouse's Stash - Unique and fun fandom goods!","":""},{"image_name":"Master Godai - 2021-06-20 23.27.33 - mastergodai-booth.jpg","display_name":"Mastergodai","website_link":"http://rascals.kemono.cafe","tags":["Comics / Graphic Novels"],"short_description":"My name is Kelvin Challenger. (aka 'Mastergodai' or just 'Godai') I've been working on webcomics since late 2009 and I want to keep doing it for as long as I can. I've loved to draw since I was 10 years old. I graduated from college in 2002 (Media Arts and Animation) but it took until mid-2009 when I decided to start my own project and create Rascals. Rascals is dedicated to an old college buddy of mine who passed away that same year. And through working on it, I think I've found something that I really enjoyed working on.","":""},{"image_name":"Michele Light - Michelelight_AC21VRBooth.jpg","display_name":"Michele Light","website_link":"https://www.michelelight.com","tags":["Stickers / Clings / Magnets","Books / Magazines","Clothing / Hats / Shirts / Pants / Leggings","Artwork (Originals / Prints / Commissions)"],"short_description":"I'm Michele Light, and I have been drawing anthropomorphic (or \"furry\") artwork since 1992. You can usually find me at the larger conventions, and I try to attend some of the smaller ones as well when time permits.","":""},{"image_name":"Mimosa Studio - Melissa Francis - booth.jpg","display_name":"Mimosa Studio","website_link":"http://www.mimosastudioart.etsy.com","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Stickers / Clings / Magnets","Lanyards","Artisan Craft Works","Sculpture / Figurines","Bags / Purses / Pouches","Keychains / Charms / Luggage Tags"],"short_description":"Cute and original illustrations and designs.","":""},{"image_name":"Moth Monarch - MothMonarch-AC21-vrdr-updated.jpg","display_name":"Moth Monarch","website_link":"https://mothmonar.ch/","tags":["Artwork (Originals / Prints / Commissions)","Bandanas","Cloth Face Masks","Necklaces / Chokers / Collars","Keychains / Charms / Luggage Tags","Buttons / Pins","Plush Animals / Toys","Stickers / Clings / Magnets","Books / Magazines"],"short_description":"Moth Monarch is an independent artist & designer creating original illustrations, merchandise, and projects for private and commercial clientele. Their keen interest in insects and animals inspires much of their work, from realistic and stylized natural forms to colorful, relatable cartoon characters. They also enjoy experimenting with the intersection of art and fashion using insect motifs and patterns in accessory and merchandise design.","":""},{"image_name":"My Arm Can Fly.jpg","display_name":"My Arm Can Fly","website_link":"http://myarmcanfly.net","tags":["Plush Animals / Toys","Sculpture / Figurines","Stickers / Clings / Magnets","Buttons / Pins","Artisan Craft Works","Artwork (Originals / Prints / Commissions)"],"short_description":"My Arm Can Fly brings all manner of wonderful, one of a kind creature companions to life as handmade needle felted sculptures! Come meet this charming array of little tricksters and familiars. These unique art dolls range from just a couple of inches tall to over 30 inches long. Felted badges and other custom items are available!","":""},{"image_name":"My Little Demon - Heather Kreiter.jpg","display_name":"My Little Demon","website_link":"http://mylittledemon.com","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Clothing / Hats / Shirts / Pants / Leggings","Books / Magazines","Stickers / Clings / Magnets","Bags / Purses / Pouches","Mousepads","Fursuits / Fursuit parts / Fursuit Accessories","Plush Animals / Toys"],"short_description":"Heather V. Kreiter is the sole creator of a dark parody of My Little Pony called My Little Demon. A Garbage Pail Kids for a new generation, if you will. While her creations may be cute, they are most definitely not for children as they have names such as Porny Delight, Autopsy Turvy, and Wang LoHung. She started MLD in 2007 as just 6 drawings and has expanded her brand to over 60 characters that have been produced as Plush Toys, Limited and Open edition prints, calendars, coloring books, Limited Edition Figurines, personal checks, and coming soon, a card game!","":""},{"image_name":"Mythimorph_AC_Booth.jpg","display_name":"Mythimorph Labs","website_link":"https://shop.mythimorph.com","tags":["Stickers / Clings / Magnets","Clothing / Hats / Shirts / Pants / Leggings","Artwork (Originals / Prints / Commissions)","Buttons / Pins"],"short_description":"Design, Create, Transform! The artwork of Riley Carbaugh. \"Mythimorph Labs\" offers specialty services in design, costumes, illustration, & animation. I offer many customizable, unique products to help you better express your inner self including fursuits, shirts, hoodies, decals, pins, décor, & accessories.","":""},{"image_name":"Neurotic_Sphynx - Ash Baus - _AC_Table.jpg","display_name":"Neurotic Sphynx","website_link":"https://www.etsy.com/shop/NeuroticSphynx","tags":["Stickers / Clings / Magnets","Buttons / Pins","Mugs / Cups / Ceramics / Coasters","Keychains / Charms / Luggage Tags"],"short_description":"Welcome to the shoppe of Neurotic Sphynx! We specialize in enamel pins and vinyl stickers. New products are added all the time, so check back frequently! Please considering supporting this queer small business owner <3","":""},{"image_name":"Nevaeh-Lee - Nevaeh-lee Boooth.jpg","display_name":"Nevaeh-lee","website_link":"http://nevaeh-lee.weebly.com","tags":["Stickers / Clings / Magnets","Buttons / Pins","Artwork (Originals / Prints / Commissions)"],"short_description":"You can call me Nevaeh! I create cute and happy art as well as have a monthly sticker club~ USE CODE ANTHROCON2021 FOR 15% OFF ON MY ETSY DURING ANTHROCON!","":""},{"image_name":"Nightengale Needles - nightengaleneedlesbooth.jpg","display_name":"Nightengale Needles","website_link":"http://www.nightengale.org/anthroconvrdr","tags":["Plush Animals / Toys","Pillows / Pillowcases","Clothing / Hats / Shirts / Pants / Leggings"],"short_description":"We believe that Nobody has to live without Joy.  Established in 2008, expanded in 2014, and incorporated in 2018, Nightengale Needles, LLC is a small business with a big heart. We keep a particular focus on offering LGBTQA+ PRIDE themed items, because some people, ourselves among them, need to know they're not alone when facing the world outside their computer.","":""},{"image_name":"Otterly Amazing - Jarold Sutton - Booth.jpg","display_name":"Otterly Amazing","website_link":"https://www.etsy.com/shop/OtterlyAmazingStudio","tags":["Fursuits / Fursuit parts / Fursuit Accessories","Pillows / Pillowcases","Masks / Costuming"],"short_description":"Otterly Amazing makes costume tails, ears and paws.","":""},{"image_name":"Pacific Autumn - 01_ac_virtual_table_pacificautumn.jpg","display_name":"Pacific Autumn","website_link":"https://pacificautumn.com/","tags":["Artwork (Originals / Prints / Commissions)","Clothing / Hats / Shirts / Pants / Leggings","Bags / Purses / Pouches","Stickers / Clings / Magnets","Books / Magazines"],"short_description":"Ace graduated from MICA in 2013 with a BFA in Illustration and later received a Certificate of Specialization in UI/UX Design from CalArts in 2020. Ace’s studio, Pacific Autumn, opened for business in 2014 – and with a passion for art and a love for the mysterious, the artist has traveled all across North America, selling curious goods and exploring the unknown. When not drawing or writing, Ace can be spotted sipping coffee with the local cryptids, walking Dharma the cat, or gardening with friendly neighborhood ghosts.","":""},{"image_name":"Panda Fuzz.jpg","display_name":"Panda Fuzz","website_link":"https://www.youtube.com/c/jawbreakerxd","tags":["Clothing / Hats / Shirts / Pants / Leggings","Stickers / Clings / Magnets","Artwork (Originals / Prints / Commissions)"],"short_description":"JawbreakerXD is a poodle on YouTube bringing you savage gaming content IN FURSUIT! She has an upcoming merch store with awesome stuff from T-shirts to stickers to art commissions! Swag for gamers AND furries! Check her out!","":""},{"image_name":"Plesiosaur Bones.jpg","display_name":"Plesiosaur Bones","website_link":"http://korybing.storenvy.com","tags":["Comics / Graphic Novels","Books / Magazines","Buttons / Pins","Necklaces / Chokers / Collars","Keychains / Charms / Luggage Tags","Clothing / Hats / Shirts / Pants / Leggings","Artwork (Originals / Prints / Commissions)"],"short_description":"Kory Bing is a Portland-based illustrator with a focus on animals that don't exist and animals that don't exist anymore. She's the award-winning creator of the long-running webcomic Skin Deep and illustrator of the InCryptid Field Guide by Seanan McGuire. When she's not drawing monsters, she's yelling about dinosaurs.","":""},{"image_name":"Princess Rei - princessrei-vrbooth.jpg","display_name":"Princess Rei's Art","website_link":"http://amethystvalley.com","tags":["Artwork (Originals / Prints / Commissions)","Books / Magazines","Sculpture / Figurines","Stickers / Clings / Magnets","Pillows / Pillowcases","Keychains / Charms / Luggage Tags","Lanyards","Bracelets / Cuffs / Wristbands"],"short_description":"My current ongoing project is my Tribe of Shawri comic, but I also have a very busy off-screen lifestyle. I'm unsure when I'm able open for commissions, so every bit of support helps me continue work on my comic, and is greatly appreciated! My life is simple, and I try to make the best of it. I enjoy arts, crafts, nature, animals, and food! I attend conventions regularly to sell art, commissions, or to network among peers. I am often dreaming and working on new projects!","":""},{"image_name":"The Rainbow Kirin - Klaora - Booth.jpg","display_name":"The Rainbow Kirin","website_link":"https://www.etsy.com/shop/TheRainbowKirin","tags":["Artwork (Originals / Prints / Commissions)","Stickers / Clings / Magnets","Buttons / Pins","Keychains / Charms / Luggage Tags"],"short_description":"Just a little artist trying to make it in the big world. :)","":""},{"image_name":"Ringtail Cafe - Alecia Weber - booth.jpg","display_name":"Ringtail Cafe Productions","website_link":"http://www.ringtailcafe.com","tags":["Games (Board / Video / Tabletop)","Books / Magazines","Comics / Graphic Novels","Sculpture / Figurines","Plush Animals / Toys","Keychains / Charms / Luggage Tags"],"short_description":"Ringtail Cafe Productions is a comic & game publishing company with a variety of projects.","":""},{"image_name":"Rysingson Accessories Othello Rysingson booth.png.jpg","display_name":"Rysingson Accessories","website_link":"http://www.squareup.com/store/rysingson-accessories","tags":["Lanyards","Necklaces / Chokers / Collars","Keychains / Charms / Luggage Tags"],"short_description":"Rysingson Accessories aims to be a one stop shop for LED and light up accessories needs. We offer wearable LED merchandise ranging from Japanese Kanji badges, Bluetooth LED nametags, El Wire hoodies, LED lanyards and suspenders in various colors to light up the dancefloor or for late night \"walkies\" around town. We also have striking pendants ranging from hand-painted Slavic foxes, wolves and bears, to fenrir wolves and adorable fennecs.","":""},{"image_name":"Ryuu Daiga - Ryuudaiga-booth.jpg","display_name":"Ryuu Daiga","website_link":"https://www.etsy.com/shop/ryuudaigaart/","tags":["Cloth Face Masks","Buttons / Pins","Keychains / Charms / Luggage Tags","Stickers / Clings / Magnets","Clothing / Hats / Shirts / Pants / Leggings","Pillows / Pillowcases","Lanyards","Bags / Purses / Pouches","Artwork (Originals / Prints / Commissions)","Earrings / Body Jewelry"],"short_description":"Original and fandom merch. I'm a Texan Anime-inspired original and fan-artist. I've been drawing for over ten years and have a burning passion for my work. You can find me most active on Twitter at @RyuuDaiga","":""},{"image_name":"Samoht Lion - Samoht-Lion-Creations-booth.jpg","display_name":"Samoht Lion Creations","website_link":"https://www.samohtlioncreations.com/","tags":["Artwork (Originals / Prints / Commissions)","Artisan Craft Works","Buttons / Pins","Keychains / Charms / Luggage Tags"],"short_description":"Furry Artist, Brony artist, lion king fan artist, Star Wars artist, papercraft creator, cricut user, vector artist.","":""},{"image_name":"Sanguine Games - Norman Rafferty - Anthrocon 2021 table texture.jpg","display_name":"Sanguine Games","website_link":"http://sanguinegames.com","tags":["Games (Board / Video / Tabletop)","Books / Magazines","Comics / Graphic Novels"],"short_description":"We are the designers of Sanguine Games. We love to make games. Ironclaw/Jadeclaw, Myriad Song, Madcap, Farflung, Urban Jungle, Noggle Stones, Bleeding Edge","":""},{"image_name":"Scratch's Den - Joyce Swope - 2048.jpg","display_name":"Scratch's Den","website_link":"https://www.etnaprintcircus.com/category/scratch-s-den","tags":["Stickers / Clings / Magnets","Buttons / Pins","Clothing / Hats / Shirts / Pants / Leggings","Artwork (Originals / Prints / Commissions)"],"short_description":"We are artists and screen printers based in the Pittsburgh borough of Etna.","":""},{"image_name":"Scrolling Fantasy - Michael Albers - AC-booth.jpg","display_name":"Scrolling Fantasy","website_link":"https://scrolling-fantasy.square.site/","tags":["Artisan Craft Works","Sculpture / Figurines"],"short_description":"Scrolling Fantasy sells range of fantasy and real animal puzzle figures and scroll saw artwork. All work is hand cut on a scroll saw and then stained. No lasers or computers are harmed.","":""},{"image_name":"silentravyn - Andrea Thiessen - vrdr.jpg","display_name":"Silent Ravyn","website_link":"https://silentravyn.carrd.co/","tags":["Artwork (Originals / Prints / Commissions)","Fursuits / Fursuit parts / Fursuit Accessories","Buttons / Pins","Stickers / Clings / Magnets","Keychains / Charms / Luggage Tags"],"short_description":"I'm a mainly traditional artist focusing on animal and furry art. Most of my artwork is mixed media, using a combination of watercolor, ink, colored pencil, marker, gouache, pastel, and acrylic, but I am now branching out into digital as well. I have also recently gotten back into building fursuits.","":""},{"image_name":"Sixth Leaf Clover - Christina Yen - Booth.jpg","display_name":"SixthLeafClover Studios LLC","website_link":"http://sixthleafcloverstore.com","tags":["Artwork (Originals / Prints / Commissions)","Sculpture / Figurines","Mousepads","Buttons / Pins","Clothing / Hats / Shirts / Pants / Leggings","Pillows / Pillowcases"],"short_description":"I bring imagination and creativity to live with each drawing, costume, and design work. I strive to challenge myself with new projects, new techniques, new builds, and new ideas. Imagination has no limit. Creativity has no boundary.","":""},{"image_name":"Skulldog - Keryn Doubleday-Everett - tablefull.jpg","display_name":"Skulldog Studio","website_link":"http://www.theskulldog.com","tags":["Artwork (Originals / Prints / Commissions)","Comics / Graphic Novels","Stickers / Clings / Magnets","Plush Animals / Toys","Books / Magazines","Clothing / Hats / Shirts / Pants / Leggings","Buttons / Pins"],"short_description":"Full time concept artist, illustrator and comic wrangler. Digital: Photoshop CS5 / Analog: Watercolor mixed media, various pencils I find left on tables.","":""},{"image_name":"Sleeping Fox Art - Rebecca Wong - _ACboothv2.jpg","display_name":"Sleeping Fox Art","website_link":"https://sleepingfoxart.com/","tags":["Artwork (Originals / Prints / Commissions)","Clothing / Hats / Shirts / Pants / Leggings","Cloth Face Masks","Stickers / Clings / Magnets","Pillows / Pillowcases","Buttons / Pins","Bags / Purses / Pouches"],"short_description":"I’m a freelance illustrator who loves fantasy and creature art!","":""},{"image_name":"Sparkle Kreations - SparkleKreations.jpg","display_name":"Sparkle Kreations","website_link":"http://www.sparklekreations.com","tags":["Fursuits / Fursuit parts / Fursuit Accessories","Plush Animals / Toys","Artwork (Originals / Prints / Commissions)","Necklaces / Chokers / Collars"],"short_description":"Sparkle Kreations has been making \"Fantastically Fluffy\" furry costumes (fursuits) since 2011! We love taking on new challenges and pride ourselves in high quality, durable, and fully tested products! We offer many types of commissions and pre-made products beyond costumes; including props, tails, and individual costume pieces!","":""},{"image_name":"STABLERCAKE - Mallory Hodgkin - Dealer's den template.jpg","display_name":"STABLERCAKE Art + Illustration","website_link":"https://www.etsy.com/shop/stablercake","tags":["Cloth Face Masks","Clothing / Hats / Shirts / Pants / Leggings","Bracelets / Cuffs / Wristbands","Stickers / Clings / Magnets","Books / Magazines","Bags / Purses / Pouches","Buttons / Pins","Patches","Artwork (Originals / Prints / Commissions)","Lanyards","Keychains / Charms / Luggage Tags","Bandanas"],"short_description":"I’m Mallory Hodgkin, also known as Stabler! I make psychedelic and often fluorescent work of mainly animals and create neat merch of it!","":""},{"image_name":"Static Claws - StaticClaws-booth8bit.jpg","display_name":"Static Claws","website_link":"https://staticclaws.square.site/","tags":["Games (Board / Video / Tabletop)","Buttons / Pins","Sculpture / Figurines","Plush Animals / Toys"],"short_description":"Retro video games, collectables, Zoids, 3D prints.","":""},{"image_name":"Stone Studios - Zin Stone - StoneStudiosDealerTable.jpg","display_name":"Stone Studios","website_link":"https://stonestudios.weeblysite.com","tags":["Artwork (Originals / Prints / Commissions)","Stickers / Clings / Magnets","Fursuits / Fursuit parts / Fursuit Accessories","Buttons / Pins"],"short_description":"Full time Artist/Costume makers 🎨Run by @Zin_Stone & @DFellwing ","":""},{"image_name":"Studio Bliz - Liz Chesterman - 2021-06-25 13.35.00.jpg","display_name":"StudioBliz","website_link":"https://www.etsy.com/shop/studiobliz","tags":["Artwork (Originals / Prints / Commissions)","Buttons / Pins","Stickers / Clings / Magnets","Mugs / Cups / Ceramics / Coasters","Keychains / Charms / Luggage Tags","Lanyards","Patches","Clothing / Hats / Shirts / Pants / Leggings"],"short_description":"It's more than dragons and beer, but they're a big part. Everyone draws when they're little, some of never stop. After some brainstorming, I came up with the idea to use my love of craft beer and fantasy illustrations that became the Drachtoberfest series. I got back into watercolors for this project and look forward to where it'll take me next. But it's not all inebriated giant monsters; I still love spaaaace, and fantasy illustration, and owls, so... you'll see some repeating themes in my work! I'm trying to make more everyday useful things, for times when wallspace is lacking, hence notebooks and coasters.","":""},{"image_name":"StudioKhimera.jpg","display_name":"StudioKhimera","website_link":"https://uberquest.studiokhimera.com","tags":["Comics / Graphic Novels"],"short_description":"Welcome Stranger! To the home of UberQuest, a weekly webcomic we began in 2014. We hope you enjoy reading it as much as we enjoy making it! UberQuest is a fantasy/adventure/sci-fi webcomic! Taking place in the fantastical world of Evyr. It's a tale that follows six protagonists, Sesame, Kibbles (Kibella), Claire, Farron, Mason and Kylar on a quest to solve an archaic mystery of ancient artifacts known as Ubers. Along the way, the group finds themselves entangled in a world that is increasingly treacherous, with a war on the horizon, factions gaining power with an arms-race for the power source of the Ubers all while the past of Evyr is slowly unveiled. Updates Every Sunday!","":""},{"image_name":"Talenshi Illustration and Design - Karen Swartz -Dealer's den-VR-anthrocon-table-2021-smaller.jpg","display_name":"Talenshi Illustration & Design","website_link":"http://www.talenshistudios.com","tags":["Buttons / Pins","Books / Magazines","Games (Board / Video / Tabletop)","Artwork (Originals / Prints / Commissions)","Artisan Craft Works"],"short_description":"We are a team of creators that create beautiful, useful objects to bring a little magic to everyday life! Karen's illustrations are on the covers of our journals, and she also works on researching and developing new products for us to make together. Justin does wildlife and product photography, and works on researching and developing new products for us to make together. We make everything in our home studio (or what some might call a dining room) in St. Louis, MO. All our journals are engraved on our Glowforge laser that lives in our tiny, well ventilated back room.","":""},{"image_name":"That One Pink Dog Studios - Cass Pink Pupper - AnthroCon Dealers Template.jpg","display_name":"That One Pink Dog Studios","website_link":"http://thatonepinkdog.studio","tags":["Fursuits / Fursuit parts / Fursuit Accessories","Buttons / Pins","Stickers / Clings / Magnets","Mugs / Cups / Ceramics / Coasters","Keychains / Charms / Luggage Tags","Clothing / Hats / Shirts / Pants / Leggings","Cloth Face Masks"],"short_description":"Digital & Traditional Artist, Premade & Custom Fursuit Maker - We are a Fursuit & Merchandise making company that specializes in bringing toony animals to life through costume and other tangible goods! ","":""},{"image_name":"Two Tail Creations - Randi Watson - booth.jpg","display_name":"Two Tail Creations","website_link":"http://www.twotailcreations.com","tags":["Clothing / Hats / Shirts / Pants / Leggings","Cloth Face Masks","Plush Animals / Toys","Artisan Craft Works"],"short_description":"Explore all my creations including: crocheted hats, crocheted shawls, crocheted blankets, paintings, colored pencil drawings, and illustrations.","":""},{"image_name":"Vin Vulpis - Vincenzo C Brancato - booth.jpg","display_name":"VinVulpis","website_link":"https://www.vinvulpis.com/commission-info/","tags":["Artwork (Originals / Prints / Commissions)","Pillows / Pillowcases","Buttons / Pins"],"short_description":"HI, I'M VINVULPIS - Artist, Animator, Music Composer(occasionally). Have an Interest in gamedev.","":""},{"image_name":"Viw's Art - Ben McFadden - booth.jpg","display_name":"Viw's Art","website_link":"https://www.furaffinity.net/user/viwrastupr","tags":["Artwork (Originals / Prints / Commissions)","Pillows / Pillowcases"],"short_description":"Does and Dragons is the comic I'm creating. It features a group of winged deer in their lives and adventures.","":""},{"image_name":"Warhorse Workshop - Daniel Axtman - Soap Pony AC21 VRDR Booth 72dpi.jpg","display_name":"Warhorse Workshop - Soap Pony","website_link":"http://etsy.com/shop/deserthorsedesign","tags":["Cloth Face Masks","Clothing / Hats / Shirts / Pants / Leggings","Stickers / Clings / Magnets","Necklaces / Chokers / Collars","Bracelets / Cuffs / Wristbands","Buttons / Pins","Fursuits / Fursuit parts / Fursuit Accessories","Mugs / Cups / Ceramics / Coasters"],"short_description":"Warhorse Workshop specializes in made to order leather, nylon, Glass, Vinyl and Rubber products to include; Tshirts, Harnesses, Collars, Kigurumi and more!","":""},{"image_name":"WearCat Creations - D68CDA9C-C32B-4D11-B373-45A6F17D1919.jpg","display_name":"WearCat Creations","website_link":"http://www.wearcatcreations.com","tags":["Fursuits / Fursuit parts / Fursuit Accessories","Buttons / Pins","Stickers / Clings / Magnets"],"short_description":"We are a small costume and prop-making studio, located in Charlotte, NC. We focus mainly on character costumes (also known as fursuits), whether you submit an already designed character or leave it up to us! Our techniques are always improving, so check back often for new features and updates. Utilizing 3D printing and 20+ years of artistic experience with a huge variety of media, we like to experiment! Don’t see us offering something you’re looking for? Drop us a line! ","":""},{"image_name":"Windfalcon (Brenda Lyons) - windfalcon_tabledisplay2.jpg","display_name":"Windfalcon","website_link":"http://www.falconmoon.com","tags":["Artwork (Originals / Prints / Commissions)","Necklaces / Chokers / Collars","Earrings / Body Jewelry","Bracelets / Cuffs / Wristbands"],"short_description":"The fantasy and wildlife illustration gallery of Brenda Lyons. My books are available in most bookstores, including Barnes and Noble, as well as online retailers such as Amazon. Most of my work is watercolor, and I also work in acrylic, ink and graphite. Since 2010 I have worked with leather, creating tooled masks and jewelry hand-painted with acrylics.","":""}];